import TypeIt from "typeit";

export default (function() {
    if (document.querySelector('.js-type-it-title')) {
        new TypeIt(".js-type-it-title", {
            speed: 100,
            startDelay: 2000,
            waitUntilVisible: true
        })
            .type("Узнай, какой ты овощ")
            .pause(1500)
            .delete(4)
            .type("<s>овощ</s> дол")
            .pause(500)
            .delete(3)
            .pause(500)
            .type("<span>дольщик</span>")
            .go();
    }
}());