export default (function() {
    if (document.querySelector('.pop-up')) {
        const popUp = document.querySelector('.pop-up'),
              popUpOpen = document.querySelector('.js-modal-open'),
              popUpClose = document.querySelector('.js-modal-close'),
              popUpContent = document.querySelector('.js-modal-content'),
              scrollWidth = window.innerWidth - document.documentElement.clientWidth;

        popUpOpen.addEventListener('click', () => {
            popUp.classList.add('pop-up--active');
            document.body.style.overflowY = 'hidden';
            document.body.style.paddingRight = `${scrollWidth}px`;
        });

        popUpClose.addEventListener('click', () => {
            popUp.classList.remove('pop-up--active');
            document.body.style.overflowY = '';
            document.body.style.paddingRight = '';
        });

        document.addEventListener('keydown', (e) => { 
            if (e.code === 'Escape' && popUp.classList.contains('pop-up--active')) {
                popUp.classList.remove('pop-up--active');
                document.body.style.overflowY = '';
                document.body.style.paddingRight = '';
            }
        });

        popUp.addEventListener('click', (e) => {
            popUp.classList.remove('pop-up--active');
            document.body.style.overflowY = '';
            document.body.style.paddingRight = '';
        })

        popUpContent.addEventListener('click', (e) => {
            e.stopPropagation();
        })
    }
}());