export default (function() {
    if (document.querySelector('.quiz')) {
        const DATA = [
            {
                question: 'Вопрос 1',
                answers: [
                    {
                        id: '1',
                        value: 'Ответ 1',
                        correct: true,
                    },
                    {
                        id: '2',
                        value: 'Ответ 2',
                        correct: false,
                    },
                    {
                        id: '3',
                        value: 'Ответ 3',
                        correct: false,
                    },
                    {
                        id: '4',
                        value: 'Ответ 4',
                        correct: false,
                    }
                ]
            },
            {
                question: 'Вопрос 2',
                answers: [
                    {
                        id: '5',
                        value: 'Ответ 5',
                        correct: true,
                    },
                    {
                        id: '6',
                        value: 'Ответ 6',
                        correct: false,
                    },
                    {
                        id: '7',
                        value: 'Ответ 7',
                        correct: false,
                    },
                    {
                        id: '8',
                        value: 'Ответ 8',
                        correct: false,
                    }
                ]
            },
        ];

        let localResults = {}; 
        
        const quiz = document.querySelector('.quiz'),
              indicator = document.querySelector('.js-indicator'),
              questions = document.querySelector('.js-questions'),
              btnNext = document.querySelector('.js-btn-next'),
              btnResult = document.querySelector('.js-btn-result'),
              sectionTestResult = document.querySelector('.js-test-result'),
              sectionGift = document.querySelector('.js-gift'),
              btnRepeat = document.querySelector('.js-repeat-test');

        const renderQuestions = (index) => {
            questions.dataset.currentStep = index;

            const renderAnswers = () => DATA[index].answers
                .map((answer) => `
                    <li class="quiz__list-item">
                        <label class="text quiz__label">
                            <input type="radio" class="quiz__input" name=${index} value=${answer.id}>
                            <span class="quiz__radio"></span>
                            ${answer.value}
                        </label>
                    </li>
                `)
                .join('');

            questions.innerHTML = `
                <div class="quiz__wrapper-item">
                    <p class="quiz__question">${DATA[index].question}</p>
                    <ul class="quiz__list">${renderAnswers()}</ul>
                </div>
                <div class="quiz__wrapper-item">
                    <p class="quiz__item-text js-indicator">Вопрос ${index + 1} из ${DATA.length}</p>
                </div>
            `;
        }

        quiz.addEventListener('change', (event) => {
            if (event.target.classList.contains('quiz__input')) {
                localResults[event.target.name] = event.target.value;
                btnNext.disabled = false;
                btnNext.classList.add('btn--transparent-black');
                btnNext.classList.remove('btn--disabled');
            }
        });

        quiz.addEventListener('click', (event) => {
            if (event.target.classList.contains('js-btn-next')) {
                const nextQuestionIndex = Number(questions.dataset.currentStep) + 1;

                if (DATA.length === nextQuestionIndex) {
                    // переход к результатам
                    btnResult.classList.remove('d-none');
                    btnNext.classList.add('d-none');
                    sectionTestResult.classList.remove('d-none');
                    sectionGift.classList.remove('d-none');

                    btnRepeat.addEventListener('click', () => {
                        localResults = {};
                        renderQuestions(0);
                        sectionTestResult.classList.add('d-none');
                        sectionGift.classList.add('d-none');
                        btnResult.classList.add('d-none');
                        btnNext.classList.remove('d-none');
                        btnNext.classList.remove('btn--transparent-black');
                        btnNext.classList.add('btn--disabled');
                    });
                } else {
                    // переход к след вопросу
                    renderQuestions(nextQuestionIndex);
                    btnNext.classList.remove('btn--transparent-black');
                    btnNext.classList.add('btn--disabled');
                }
                
                btnNext.disabled = true;
            }
        });

        renderQuestions(0);
    }
}());